body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display:flex;
  text-align: center;
  background-color: #282c34;
  color: white;
  font-size: calc(10px + 2vmin);
  min-height: 100vh;
}

.DaysTextColumnTable{
  empty-cells:show;
}

.Calendar{
  flex: 1 1;
  display:flex;
  justify-content: center;
  height: 75%;
}

.Rating{
  border-radius: 50%;
  height:15px;
  width:15px;
  color: white;
}

.Buffer{
  flex: 2 1;
}

.TableOfContents{
  flex-direction:column;
  border-style:solid;
  border-color:white;
  border-width: 0.25vh;
  width: 5vh;
  margin: 1vh;
  height: 70vh;
}

.TableOfContentsHeader{
  font-size:10px;
}
